.Articlebody{
  box-sizing: border-box;
}

.Articlemain{
  float: right;
  width: 70%;
  margin: 0 2rem 10rem 0;
}



.Catelog{
  position:fixed;
  top:20%;
  left: 80px;
  width: 20%;
  padding-left: 0;
	border-left: 1px solid #efefef;
}

.Catelog a {
	text-decoration: none;
	display: block;
	padding: .125rem 0;
	color: #ccc;
	transition: all 50ms ease-in-out;
}

.Catelog a:hover,
.Catelog a:focus {
	color: #666;
}

.is-active-link{
  font-weight:700
}

.toc-list{
  margin:0;
  padding-left:10px;
}

a.toc-link{
  color:currentColor;
  height:100%
}

.toc-link::before{
  background-color:#EEE;
  content:' ';
  display:inline-block;
  height:inherit;left:0;
  margin-top:-1px;
  position:absolute;
  width:2px
}

.is-active-link::before{
  background-color:#0a216b
}


.return{
  width: 80px;
  height: 25px;
}